<template>
	<div style="position:fixed;width:100%">
	
	<div class="container">
	<div class="row clearfix">
		<div class="col-md-12 column">
			<nav class="navbar navbar-default" role="navigation">
				<div class="navbar-header">
					 <button type="button" class="navbar-toggle" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1"> <span class="sr-only">Toggle navigation</span><span class="icon-bar"></span><span class="icon-bar"></span><span class="icon-bar"></span></button> <a class="navbar-brand" href="/">Mmy's Blog</a>
				</div>
				
				<div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
					
					
					<ul class="nav navbar-nav navbar-right">
						<li>
							 <router-link to="/">博客</router-link>
						</li>
						<li>
							 <router-link to="/add">写博客</router-link>
						</li>
						<li>
							 <a href="#">记录</a>
						</li>
						<li>
							 <a href="#">设计图</a>
						</li>

						<form class="navbar-form navbar-left" role="search">
						<div class="form-group">
							<input type="text" class="form-control" v-model="search" />
						</div>
						 <input  type="button" class="btn btn-default"  v-on:click="getsearch()" value="搜索">
						</form>

					</ul>
					
				</div>
				
			</nav>
		</div>
	</div>
</div>
</div>

</template>

<script>
	import '../assets/app.css'
	export default{
		props:['searchvalue'],
		name:'l-header',
		data(){
			return {
				 search:""

			}
		},
		methods:{
			getsearch(){
				this.searchvalue(this.search.toLowerCase());  //转换为小写后传值 
			}
		}
	}
</script>

<style scoped>

	*{
		overflow: hidden;
	}
</style>



