<template>

  <div id="addblog">
    <h1 >添加博客</h1>
     <div v-if="!display">
        <form action="" >
          <label  class="blog-font-title">博客标题</label>
           <input type="text" v-model="blog.header_title"  class="form-control" >
          <label >博客内容</label>
            <textarea type="text" v-model="blog.body_container" class="v-area form-control"></textarea>
        </form>

        <div id="checkboxes">
          <label for="">vue.js</label>
             <input type="checkbox" value="vue.js" v-model="blog.checkbox_container">

          <label for="">Node.js</label>
             <input type="checkbox" value="Node.js" v-model="blog.checkbox_container">

          <label for="">React.js</label>
             <input type="checkbox" value="React.js" v-model="blog.checkbox_container">

          <label for="">Angular4.js</label>
             <input type="checkbox" value="Angular4.js" v-model="blog.checkbox_container">

          <label for="">Jquery.js</label>
             <input type="checkbox" value="Jquery.js" v-model="blog.checkbox_container">
        
        </div>
        <div>
          <p style="font-size: 20px;color: black">作者:</p>
          <select v-model="blog.blog_author"  class="author1">
            <option v-for="author in authors">
                {{author}}
            </option>
          </select>
          <button v-on:click="post" class="btn btn-primary">提交博客</button>
        </div>

        
            
        
    </div>   
    <div v-if="display">
      <h1>Release Success</h1>
      <h2><router-link to="/">Go On</router-link></h2>
    </div>
    

  </div>
</template>

<script>
export default {
  name: 'addblog',
  data () {
    return {
      blog:{
        header_title:"",
        body_container:"",
        checkbox_container:[],
        blog_author:""
      },
      authors:["Mmy","Tom","Ben"],
      display:false
    }
  },
  methods:{
    post:function(){
      this.$http.post('https://vueblog-12bf2.firebaseio.com/posts.json',this.blog)  //post数据到firebase
      .then(function(data){
          console.log(data);
          this.display=true;
      })   
    }
  }
}
</script>


<style scoped>
  .v-area{
    height: 500px;
  }
  .author1{
    width: 200px;
    height: 30px;
    background:rgba(0,0,0,0.5);
    color: white;
  }
  .btn{
    margin: 5px;
  }

</style>



