<template>
	<div  id="show-blogs">
		<h1>All Blog</h1>
		<div  class="single-blog">
			<ul >
				<li v-for="content in filteredBlogs">
				<h3 v-on:click="getid()">{{content.header_title|to-uppercase}}</h3>
				</li>
			</ul>
			<div>
				<h1 v-if="display">没有找到你想要的文章！</h1>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		props:['searchaa'],
		name:'show-blogs',
		data:function(){
			return	{
				blogs:[],
				search:this.searchaa,
				display:false,

				
			}
		},
		created(){
			/*
			this.$http.get('https://jsonplaceholder.typicode.com/posts').then(
				function(data){
					this.blogs=data.body.slice(0,15); 
					console.log(this.blogs);
					//this.blogs=data.body;
				}
				)
				*/
			
			this.$http.get('https://vueblog-12bf2.firebaseio.com/posts.json')  //请求数据 
			.then(function(data){
				return data.json()
			})
			.then(function(data){
				var i=new Array();
				for(let key in data){
					data[key].id=key;
					i.push(data[key]);
				}
				this.blogs=i;
				console.log(this.blogs);
			})
			
		},
		computed:{
			filteredBlogs:function(){
				return this.blogs.filter((blog)=>{
					return blog.header_title.match(this.searchaa);			
				})
			}
		},
		methods:{
			getid(){
				console.log(this.blogs);
			}
		}

	}	
</script>

<style scoped>
	li{
		background: rgba(255,255,255,0.6);
		box-shadow: 3px 3px 10px #888888;
		margin-top: 10px; 
		min-height:120px;	
	}



</style>


