<template>
	<div >
		<v-header :searchvalue="parent"></v-header>
		<div id="body" >
			<div class="container-H" style="margin-top: 80px;">
				<v-showblog :searchaa='search' style="padding-top: 20px;"></v-showblog>
			</div>
			<div class="introduction-H">
			</div>		
		</div>
	</div>
</template>

<script>
	import ShowBlog from './ShowBlog'
	import Header  from '../Header'
  	export default{
		props:['searcha'],
		name:'',
		data:function(){
			return{
				search:''
			}
		},
		components:{
			'v-showblog':ShowBlog,
			'v-header':Header
		},
		methods:{
			parent:function(data){
				this.search=data;
			}

		}
	}
</script>

<style scoped>
	#body{
		display: flex;	
		flex-wrap: wrap;
		justify-content: center;
		
		
	}
	.container-H{
		width: 60%;
		background: rgba(255,255,255,0.3);
		margin-right: 50px;
		text-align: center;
		max-width: 800px;
		padding:20px;
		min-height: 1000px;
		box-shadow: 1px 1px 1px #888888;		
	}
	.introduction-H{
		width: 300px;
		height: 500px;
		background:rgba(255,255,255,0.6);
		margin:150px 20px;
		margin-left: 50px;


	}

	@media (max-width: 1224px){
		.introduction-H{
			display: none;
		}
		.container-H{
			width: 90%;
			margin-right: 0;
			
			
		}
	}

</style>


